import React, { useEffect } from 'react';
import { Box, Typography, Button, Paper } from '@mui/material';
import { useDocumentContext } from '../state/documentContext';
import { injectMockDocumentData } from '../utils/mockDocumentData';
import ProductPriceSummary from './ProductPriceSummary';
import { useTranslation } from 'react-i18next';
import { WhitePaperContainer } from 'pages/documents/styled-components';

const TestDocumentViewer: React.FC = () => {
    const { t } = useTranslation();
    const documentContext = useDocumentContext();
    const { updatedDocumentData } = documentContext;

    useEffect(() => {
        // Inject mock data for testing
        injectMockDocumentData(documentContext);
    }, [documentContext]);

    if (!updatedDocumentData) {
        return (
            <Box p={3}>
                <Typography variant="h6">Loading mock data...</Typography>
            </Box>
        );
    }

    const handleReloadMockData = () => {
        injectMockDocumentData(documentContext);
    };

    const products = updatedDocumentData.data?.addedProducts || [];
    console.log('All products loaded:', products.map(p => ({ name: p.name, id: p.id })));

    return (
        <WhitePaperContainer>
            <Box p={3}>
                <Typography variant="h4" gutterBottom>
                    Test Document Viewer - Mock Data
                </Typography>

                <Button
                    variant="contained"
                    onClick={handleReloadMockData}
                    sx={{ mb: 3 }}
                >
                    Reload Mock Data
                </Button>

                <Typography variant="h6" gutterBottom>
                    Document Hash: {updatedDocumentData.hash}
                </Typography>

                <Typography variant="body1" gutterBottom>
                    Client: {updatedDocumentData.data?.selectedClient?.companyName}
                </Typography>

                <Typography variant="h6" gutterBottom sx={{ mt: 3 }}>
                    Total Products Loaded: {products.length}
                </Typography>

                <Typography variant="h6" gutterBottom sx={{ mt: 3 }}>
                    All Products (including those without issues):
                </Typography>

                {products.map((product, index) => (
                    <Paper key={index} sx={{ p: 2, mb: 2 }}>
                        <Typography variant="h6" gutterBottom>
                            {product.name} (ID: {product.id})
                        </Typography>

                        <Typography variant="body2" gutterBottom>
                            Price: {product.price} €
                        </Typography>

                        <Typography variant="body2" gutterBottom>
                            Discount: {product.discount}%
                        </Typography>

                        <Typography variant="body2" gutterBottom color="error">
                            discountedPrice: {(product as any).discountedPrice === null ? 'null' : (product as any).discountedPrice}
                        </Typography>

                        <Typography variant="body2" gutterBottom>
                            Components: {product.components?.length || 0}
                        </Typography>

                        {/* Test the ProductPriceSummary component */}
                        <Box sx={{ mt: 2 }}>
                            <Typography variant="subtitle1" gutterBottom>
                                Product Price Summary:
                            </Typography>
                            <ProductPriceSummary
                                product={product}
                                components={product.components || []}
                            />
                        </Box>

                        {/* Show components with null discountedPrice */}
                        {product.components && product.components.length > 0 && (
                            <Box sx={{ mt: 2 }}>
                                <Typography variant="subtitle2" gutterBottom>
                                    Components with null discountedPrice:
                                </Typography>
                                {product.components
                                    .filter(comp => (comp as any).discountedPrice === null)
                                    .map((component, compIndex) => (
                                        <Box key={compIndex} sx={{ ml: 2, mb: 1 }}>
                                            <Typography variant="body2" color="error">
                                                • {component.name}: price={component.price}, discount={component.discount}, discountedPrice=null
                                            </Typography>
                                        </Box>
                                    ))}
                            </Box>
                        )}
                    </Paper>
                ))}

                <Typography variant="h6" gutterBottom sx={{ mt: 3 }}>
                    Expected Behavior:
                </Typography>

                <Typography variant="body2" component="div" sx={{ ml: 2 }}>
                    <ul>
                        <li>Product Price Summary should show "0.00 €" instead of "NaN €" for component totals</li>
                        <li>Discounted Price displays should show "0.00 €" instead of "NaN €"</li>
                        <li>All price calculations should work correctly despite null discountedPrice values</li>
                    </ul>
                </Typography>
            </Box>
        </WhitePaperContainer>
    );
};

export default TestDocumentViewer; 